import React from 'react';
import styles from './Users.module.scss'
import * as axios from 'axios';
import PaginationComponent from '../common/Pagination/PaginationComponent';
import { NavLink } from 'react-router-dom';

const Users = (props) => {

    return <>
        <PaginationComponent totalUsersCount={props.totalUsersCount}
            pageSize={props.pageSize}
            currentPage={props.currentPage}
            onPageChanged={props.onPageChanged} />


        {
            props.users.map(u => <div key={u.id} >

                <span>
                    <div>
                        <NavLink to={'/profile/' + u.id}>
                            <img src={u.photos.small != null ? u.photos.small : "https://cs8.pikabu.ru/post_img/2017/01/24/8/1485265069188571085.jpg"} className={styles.userphoto} />
                        </NavLink>
                    </div>
                    <div>
                        {
                            u.followed
                                ? <button disabled={props.followingInProgress} onClick={() => {
                                    props.toogleFollowingProgress(true);
                                    axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`,
                                        {
                                            withCredentials: true,
                                            headers: {
                                                "API-KEY": process.env.REACT_APP_API_KEY
                                            }
                                        }

                                    )
                                        .then(response => {
                                            if (response.data.resultCode == 0) { props.unfollow(u.id) }
                                        });
                                    props.toogleFollowingProgress(false);


                                }}> UnFollow</button>
                                : <button disabled={props.followingInProgress} onClick={() => {
                                    props.toogleFollowingProgress(true);
                                    axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {},

                                        {
                                            withCredentials: true,
                                            headers: {
                                                "API-KEY": process.env.REACT_APP_API_KEY
                                            }

                                        })
                                        .then(response => {
                                            if (response.data.resultCode == 0) { props.follow(u.id) }
                                        });
                                    props.toogleFollowingProgress(false);

                                }}> Follow</button>
                        }
                    </div>
                </span>
                <span>
                    <span className={styles.content}>
                        <div> {u.name} </div>
                        <div> {u.status} </div>
                    </span>
                </span>
            </div>
            )
        }
    </>
}

export default Users;