import React from 'react'
import {connect} from 'react-redux';
import Users from './Users';
import { setUsers, unfollow, follow, setCurrentPage, setTotalUsersCount,toogleIsFetching,toogleFollowingProgress} from '../../redux/users-reducer';
import CircularIndeterminate from '../common/preloader/CircularIndeterminate';
import {usersAPI} from "../../api/api"


class UsersApiComponent extends React.Component {

    componentDidMount() {
        this.props.toogleIsFetching(true);
        usersAPI.getUsers(this.props.currentPage,this.props.pageSize).then ( data => {
            this.props.toogleIsFetching(false);
            this.props.setUsers(data.items);
            this.props.setTotalUsersCount(data.totalCount);

        });
    }
       

    onPageChanged = (pageNumber) => {
        this.props.toogleIsFetching(true);
        this.props.setCurrentPage(pageNumber);      
        usersAPI.getUsers(pageNumber,this.props.pageSize).then ( data => {
            this.props.toogleIsFetching(false);
            this.props.setUsers(data.items);
        });
    }
    render() {
        return <>
        {this.props.isFetching ? <CircularIndeterminate/> : null }
        <Users
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        onPageChanged={this.onPageChanged}
        users={this.props.users}
        unfollow={this.props.unfollow}
        follow={this.props.follow}   
        currentPage={this.props.currentPage}
        toogleFollowingProgress={this.props.toogleFollowingProgress}
        followingInProgress={this.props.followingInProgress}
        />
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: state.UsersPage.users,
        pageSize: state.UsersPage.pageSize,
        totalUsersCount: state.UsersPage.totalUsersCount,
        currentPage: state.UsersPage.currentPage,
        isFetching: state.UsersPage.isFetching,
        followingInProgress: state.UsersPage.followingInProgress

    }
}

export default connect(mapStateToProps,
    {   follow, 
        unfollow, 
        setUsers, 
        setCurrentPage, 
        setTotalUsersCount, 
        toogleIsFetching,
        toogleFollowingProgress
    })(UsersApiComponent);